@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 5;
  padding: 10;
  box-sizing: border-box;
  z-index: 2;
}

body {
  background-image: linear-gradient(120deg, #A9AAFF 0%, #A9DDFF  100%);
  color: white;
  font-family: "Poppins", sans-serif;
  max-height: 100vh;
  max-width: 100vw;
}

.row{
  display: flex;
  align-items: center;
  justify-content: center; 
  margin: 30px;
}

.nav-link {
  color: black;
}

.general-button {
  border-radius: 20px;
  font-size: 32px;
  background-color: white;
  color: black;
}

.camera-button {
  border-radius:70px;
  overflow: none;
}

.nav.nav-pills {
  display: flex;
  align-items: center;
  justify-content: space-evenly;

}

.nav {
  border-radius: 50px;
}

.nav-link.active {
  color:white !important;
  background-color:#007bff !important;
  border-radius: 20px;
  width:100%
}

.nav-link {
  font-size:36px;
  text-align: center;
  color: black;
  background-color: white;
  margin: 3px;
  width:100%;
}

.tab-content {
  font-size: 48px;
  text-align: center;
  min-height: 80vh;
  max-height: 82vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.circles {
  display:flex;
  align-items: center;
  justify-content: center;
  position:fixed;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.circle1, .circle2, .circle3 {
  background: white;
  background: linear-gradient(
    to right bottom,
    rgba(10, 145, 255, 0.8),
    rgba(45, 77, 255, 0.3)
  );
  opacity: 0.5;
  position: absolute;
  border-radius: 50%;
  z-index: 1;
}

.circle1 {
  height: 30rem;
  width: 30rem;
}

.circle2 {
  height: 35rem;
  width: 35rem;
}

.circle3 {
  height: 40rem;
  width: 40rem;
}
